import * as React from 'react';
import {
    Table, Button, Drawer, Radio, Cascader, Input, Icon, Upload, message ,Modal
} from 'antd';
import { getPetTypeOptions } from '../../../util/petTypeOptions';
import './index.less';
import axios from '../../../util/Axios';
import { sendToQiniu, concatImageString } from '../../../util/sendToQiniu';
import moment from 'moment'

const RadioGroup = Radio.Group;
const Dragger = Upload.Dragger;

const PopImage = (imgSrc: string) => (
    <img src={imgSrc} alt="" style={{ width: "100%" }} />
)

export default class ServiceDivide extends React.Component<any, any> {

    public state = {
        files: [],
        updateId: 1, // edit id id
        selectedRowKeys: [], // Check here to configure the default column
        selectedPetKeys: [],
        selectedPetTypeKeys: [],
        loading: false,
        filteredInfo: null,
        sortedInfo: "",
        visible: false,
        editVisible: false,
        value: 2,  // 
        imageUrl: "",
        defaultServiceName: "", // 
        defalutSizeValue: 6, // 
        editSizeVisible: false,  // 
        tableData: [],
        resTableData: [],
        resPetTypeTableData: [],
        showImage: false,
        imageSrc: "",
    };

    public componentDidMount() {
        const That = this
        axios({
            method: "get",
            url: "/service/list"
        }).then((res: any) => {
            if (res && res.length > 0) {
                const tableData = res.map((item: any, index: number) => {
                    return {
                        key: item.id,  // key data id 
                        name: item.name,
                        image: item.img ? item.img : "",
                        kinds: `${item.level}`,
                        status: item.status ? item.status : "----",
                        creatTime: item.ct ? moment(parseInt(item.ct,10)).format('YYYY-MM-DD H:mm:ss') : "----",
                        modifiedTime: item.mt ? moment(parseInt(item.mt,10)).format('YYYY-MM-DD H:mm:ss') : "----",
                        operation: ""
                    }
                })
                That.setState({
                    "tableData": tableData,
                    resTableData: res
                })
            }
        })
    }

    public onSelectChange = (selectedRowKeys: any) => {
        this.setState({ selectedRowKeys });
    }

    public handleChange = (pagination: any, filters: any, sorter: any) => {
        console.log('Various parameters', pagination, filters, sorter);
        this.setState({
            filteredInfo: filters,
            sortedInfo: sorter,
        });
    }

    public handleDelete = (key: any) => {
        console.log(key)
    }

    public addServiceType = () => {
        this.showDrawer()
    }

    public editServiceType = (record: any) => {
        this.setState({
            editVisible: true,
            defaultServiceName: record.name,
            updateId: record.key
        })
    }

    public showDrawer = () => {
        this.setState({
            visible: true,
        });
    };

    public deleteServiceType = () => {

        const selectedRowKeys = this.state.selectedRowKeys

        axios({
            method: "post",
            url: "/service/delete",
            data: {
                ids: selectedRowKeys
            }
        }).then((res: any) => {
            if (res) {
                message.success("")
                setTimeout(() => {
                    window.location.reload()
                }, 500);
            } else {
                message.error("")
            }
        })

    }

    public submitServiceType = () => {

        const { defaultServiceName, selectedPetKeys, imageUrl, value } = this.state

        const PID = selectedPetKeys[1] ? this.state.selectedPetKeys[1] : -1

        axios({
            method: "post",
            url: "/service/add",
            data: {
                name: defaultServiceName,
                pid: PID,
                img: imageUrl,
                level: value,
            }
        }).then((res: any) => {
            if (res) {
                message.success("")
                setTimeout(() => {
                    window.location.reload()
                }, 500);
            } else {
                message.error("")
            }
        })

    }
    public editSubmitServiceType = () => {

        const { defaultServiceName, imageUrl, updateId } = this.state

        axios({
            method: "post",
            url: "/service/edit",
            data: {
                id: updateId,
                name: defaultServiceName,
                image: imageUrl,
            }
        }).then((res: any) => {
            if (res) {
                message.success("")
                setTimeout(() => {
                    window.location.reload()
                }, 500);
            } else {
                message.error("")
            }
        })
    }

    public beforeUpload(file: any) {

        const FileList = this.state.files

        this.setState({
            files: [...FileList, file]
        })

        return false;
    }

    public handleUpload = ({ fileList, file }: { fileList: any, file: any }) => {

        const imgName = file.name


        let adminId: string | null = localStorage.getItem("adminId")

        const timeStamp = new Date().getTime().toString()
    
        if (adminId) {
            while (adminId.length < 36) {
                adminId += "x"
            }
        } else {
            alert("")
        }
    
    
        const fileName = `${adminId}${timeStamp}ao12`

        fileList.map((item:any,index:number)=>{  
            if(item.name === imgName){
                item.name = fileName
            }
            return
        })

        console.log(fileList,file)

        const upToken = localStorage.getItem('uploadToken')  // token

        if (upToken) {
            sendToQiniu(file, fileName, upToken,"ao") // 
        }

        const ImageString = concatImageString(fileList)


        this.setState({
            files: fileList,
            "imageUrl": ImageString
        })

    }
    public render() {

        const { selectedRowKeys, editSizeVisible, defalutSizeValue, tableData, resTableData, files } = this.state;

        let { filteredInfo }: any = this.state;

        filteredInfo = filteredInfo || {};

        const columns = [{
            title: '',
            dataIndex: 'name',
            key: 'name',
        }, {
            title: '',
            dataIndex: 'image',
            key: 'image',
            render: (text: any, record: any) => {

                if (record.image !== "") {

                    const imageUrlList = record.image.split(',')

                    const images = imageUrlList.map((item: string, index: number) => (
                        <a href="javascript:void(0);" key={index} onClick={() => {
                            this.setState({
                                showImage: true,
                                imageSrc: item
                            })
                        }}></a>
                    ))

                    return images

                } else {

                    return <span></span>

                }

            }
        }, {
            title: '',
            dataIndex: 'kinds',
            key: 'kinds',
            filters: [
                { text: '1', value: '1' },
                { text: '2', value: '2' },
            ],
            filteredValue: filteredInfo.kinds || null,
            onFilter: (Value: string, record: any) => record.kinds.toString().includes(Value),
        }, {
            title: '',
            dataIndex: 'status',
            key: 'status',
        }, {
            title: '',
            dataIndex: 'creatTime',
            key: 'creatTime',
        }, {
            title: '',
            dataIndex: 'modifiedTime',
            key: 'modifiedTime',
        }, {
            title: '',
            dataIndex: 'operation',
            key: 'operation',
            render: (text: any, record: any) => {
                console.log(record)
                return (
                    <a href="javascript:;" onClick={this.editServiceType.bind(this, record)}></a>
                )
            }
        }];

        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange
        };

        const hasSelected = selectedRowKeys.length > 0;

        const Options = getPetTypeOptions(resTableData, this.state.value)

        return (
            <div>
                <div style={{ marginBottom: 16, textAlign: "left" }}>
                    <Button type="primary" onClick={this.addServiceType.bind(this)} style={{ marginRight: 20 }}>
                        
                    </Button>
                    <Button type="danger" disabled={!hasSelected} onClick={() => {
                        this.deleteServiceType()
                    }}>
                        
                    </Button>

                    <span style={{ marginLeft: 8 }}>
                        {hasSelected ? `${selectedRowKeys.length}` : ''}
                    </span>
                </div>
                <div>
                    <Table rowSelection={rowSelection} columns={columns} dataSource={tableData}
                        pagination={{ pageSize: 10 }}
                        onChange={this.handleChange}
                        locale={{ filterConfirm: "", filterReset: "", emptyText: "" }}
                    />
                </div>
                <div>
                    <Drawer
                        width={500}
                        title=""
                        placement="right"
                        closable={true}
                        onClose={() => { this.setState({ visible: false, files: [] }) }}
                        visible={this.state.visible}
                    >
                        <RadioGroup onChange={(e: any) => { this.setState({ value: e.target.value }) }} value={this.state.value}>
                            <Radio value={1}></Radio>
                            <Radio value={2}></Radio>
                        </RadioGroup>
                        <div style={{ margin: "20px 0" }}>
                            {Options ? <span></span> : ""}
                            {Options ?
                                <Cascader style={{ width: "70%" }} options={Options} onChange={(val: any) => this.setState({ selectedPetKeys: val })} placeholder="" />
                                : ""}
                        </div>
                        <div>
                            <span></span>
                            <Input style={{ width: "70%" }} placeholder="" onChange={(e: any) => { this.setState({ defaultServiceName: e.target.value }) }} />
                        </div>
                        <div className="Box">
                            <span></span>
                            <Dragger className="Dragger"
                                fileList={files}
                                beforeUpload={this.beforeUpload.bind(this)}
                                onChange={this.handleUpload}
                            >
                                <p className="ant-upload-drag-icon">
                                    <Icon type="inbox" />
                                </p>
                                <p className="ant-upload-hint"></p>
                            </Dragger>
                        </div>
                        <div
                            style={{
                                position: 'absolute',
                                left: 0,
                                bottom: 0,
                                width: '100%',
                                borderTop: '1px solid #e9e9e9',
                                padding: '10px 16px',
                                background: '#fff',
                                textAlign: 'right',
                            }}
                        >
                            <Button onClick={() => { this.setState({ visible: false }) }} style={{ marginRight: 8 }}>
                                
                            </Button>
                            <Button onClick={() => {
                                this.setState({ visible: true })
                                this.submitServiceType()
                            }}
                                type="primary">
                                
                            </Button>
                        </div>
                    </Drawer>
                </div>
                <div>
                    <Drawer
                        width={500}
                        title=""
                        placement="right"
                        closable={true}
                        onClose={() => { this.setState({ editVisible: false, files: [] }) }}
                        visible={this.state.editVisible}
                    >
                        <div>
                            <span></span>
                            <Input style={{ width: "70%" }} placeholder="" onChange={(e: any) => { this.setState({ defaultServiceName: e.target.value }) }}
                                defaultValue={this.state.defaultServiceName}
                                value={this.state.defaultServiceName}
                            />
                        </div>
                        <div className="Box">
                            <span></span>
                            <Dragger
                                className="Dragger"
                                fileList={files}
                                beforeUpload={this.beforeUpload.bind(this)}
                                onChange={this.handleUpload}
                            >
                                <p className="ant-upload-drag-icon">
                                    <Icon type="inbox" />
                                </p>
                                <p className="ant-upload-hint"></p>
                            </Dragger>
                        </div>
                        {
                            editSizeVisible ?
                                <div>
                                    <span></span>
                                    <RadioGroup onChange={(e: any) => { this.setState({ defalutSizeValue: e.target.value }) }} value={defalutSizeValue}>
                                        <Radio value={6}></Radio>
                                        <Radio value={7}></Radio>
                                        <Radio value={8}></Radio>
                                    </RadioGroup>
                                </div> : ""
                        }
                        <div
                            style={{
                                position: 'absolute',
                                left: 0,
                                bottom: 0,
                                width: '100%',
                                borderTop: '1px solid #e9e9e9',
                                padding: '10px 16px',
                                background: '#fff',
                                textAlign: 'right',
                            }}
                        >
                            <Button onClick={() => { this.setState({ editVisible: false }) }} style={{ marginRight: 8 }}>
                                
                            </Button>
                            <Button onClick={() => {
                                // petTypesUpdateMutation()
                                this.editSubmitServiceType()
                            }}
                                type="primary">
                                
                            </Button>
                        </div>
                    </Drawer>
                    <Modal
                        visible={this.state.showImage}
                        onCancel={() => this.setState({ showImage: false })}
                        footer={null}
                        destroyOnClose={true}
                    >
                        {PopImage(this.state.imageSrc)}
                    </Modal>
                </div>
            </div >
        )
    }
}
